import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

class ColourList extends React.Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.colours = [
      { id: 1, rgb: 0},
      { id: 2, rgb: 0},
      { id: 3, rgb: 0},
      { id: 4, rgb: 0},
      { id: 5, rgb: 0},
      { id: 6, rgb: 0},
    ];
    }

  randomColour(){
    const random = [];
    for (let i = 0; i < 3; i++) {
      random.push(Math.floor((Math.random()*256+255)/2));
    }
    const colour = this.rgbToHex(random);
    return colour;
  }

  handleClick() {
    for (let i = 1; i < 7; i++) {
      const colour = this.randomColour();
      document.getElementById(i).style.backgroundColor = colour;
      document.getElementById(i).innerHTML = "<h2>"+colour+"</h2>";
    }
   }

   componentToHex(c) {
     const hex = c.toString(16);
     return hex.length === 1 ? "0" + hex : hex;
   }

   rgbToHex(rgb) {
     return "#" + this.componentToHex(rgb[0]) + this.componentToHex(rgb[1]) + this.componentToHex(rgb[2]);
   }

  render(){
    for (let i = 0; i < 6; i++) {
      this.colours[i].rgb = this.randomColour();
    }
    const cols = this.colours.map((colour) =>
      <Col id={colour.id} style={{backgroundColor: colour.rgb}} key={colour.id}><h2>{colour.rgb}</h2></Col>
    );

    return (
      <Container fluid="true" style={{height:"100%"}}>
        <Row id="nav">
          <button onClick={this.handleClick}>
            GENERATE
          </button>
        </Row>
        <Row id="pallete">{cols}</Row>
      </Container>
    );
  }
}

ReactDOM.render(
    <ColourList />, document.getElementById('root'));

